@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-page-slider{
	font-family: $ff-45Ligh;
	font-size: 10px;
	position: relative;
	a {
	    transition: all 0.3s ease;
	    text-decoration: none;
	}
}
.b-btns{
	width: 100%;
	position: fixed;
	bottom: 3.000em;
	left: 0;
	z-index: 80;
	@extend .b-flex;
	@extend .b-flex--center;
}
.popover-is-open{
	.b-btns{
		opacity: 0;
	}
}
.b-btn-back,
.b-btn-next{
	padding: 0 1.400em;
	border: none;
	background: none;
	border-radius: 0;
	font-family: $ff-45Ligh;
	font-size: 1.2em;
	font-weight: 400;
	letter-spacing: 0.2em;
	text-transform: uppercase;
	position: relative;
	cursor: pointer;
	z-index: 10;
	&:after{
		width: 0;
		height: 0;
		margin: 1em auto 0;
		display: block;
		content: '';
		border-left: 2.200em solid transparent;
		border-right: 2.200em solid transparent;
		position: relative;
	}
	span{
		display: block;
		font-size: 1.400em;
		line-height: 1em;
	}
}
.b-btn-back{
	color: $color-grey;
	&:after{
		border-bottom: 2.200em solid $color-grey;
	}
}
.b-btn-next{
	color: $color-green;
	&:after{
		border-top: 2.200em solid $color-green;
	}
}
.slideOne{
	.b-btn-back{
		display: none;
	}
}
.slideFive{
	.b-btn-back{
		color: $color-white;
		&:after{
			border-bottom-color: $color-white;
		}
	}
	.b-btn-next{
		display: none;
	}
}
.b-main{
	position: relative;
	z-index: 2;
}
.b-screen{
	padding: 145px 0 0;
	position: relative;
	overflow: hidden;
	@extend .b-flex;
	@extend .b-flex--middle-col;
	.b-center{
		width: 104.000em;
		height: 62.600em;
		margin: 0 auto;
		position: relative;
		z-index: 5;
	}
	.b-dialog{
		position: absolute;
	}
	&__img{
		display: block;
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% auto;
		position: absolute;
		&--door{
			width: 33.500em;
			height: 33.400em;
			background-image: url(../images/svg/door.svg);
			top: calc((50% + 9.000em) - 33.400em);
			left: calc(50% - 7.700em);
			opacity: 0;
		}
		&--buildL,
		&--buildR{
			width: calc(50% - 2em);
			height: 40.400em;
			background-image: url(../images/svg/build.svg);
			background-color: $color-bg2;
			background-position: left bottom;
			top: 0;
			z-index: 1;
		}
		&--buildL{
			left: 0;
		}
		&--buildR{
			right: 0;
		}
		&--man{
			width: 17.300em;
			height: 42.000em;
			background-image: url(../images/svg/man.svg);
			top: calc(50% - 21.400em);
			left: calc(50% - 8.600em);
			z-index: 5;
		}
		&--flower{
			width: 100%;
			top: calc(50% + 9.000em);
			left: 0;
			z-index: 2;
			&:before{
				width: 100%;
				height: 0.800em;
				display: block;
				content: '';
				background: $color-white;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
			}
			span{
				width: 6.100em;
				height: 14.400em;
				display: block;
				background-image: url(../images/svg/flower.svg);
				background-position: 0 0;
				background-repeat: no-repeat;
				background-size: 100% auto;
				position: absolute;
				top: calc(50% - 11.400em);
				right: 1.200em;
				z-index: 2;
				&:first-child{
					right: auto;
					left: 1.200em;
				}
			}
		}
		&--ipad,
		&--ipad2{
			width: 7.100em;
			height: 9.800em;
			background-image: url(../images/svg/ipad.svg);
			top: calc(50% - 8.200em);
			left: calc(50% + 4.600em);
		}
		&--ipad2{
			background-image: url(../images/svg/ipad2.svg);
			.b-dialog{
				bottom: calc(100% + 4.300em);
				left: 0.200em;
			}
		}
		&--table{
			width: 64.600em;
			height: 28.200em;
			background-image: url(../images/svg/table.svg);
			bottom: 0;
			left: calc(50% - 32.000em);
			z-index: 5;
			.b-dialog{
				bottom: calc(100% + 3.100em);
				left: calc(100% - 5.400em);
			}
		}
		&--calendar{
			width: 25.000em;
			height: 35.500em;
			background-image: url(../images/svg/calendar.svg);
			top: calc(50% - 20.600em);
			left: calc(50% - 43.200em);
			z-index: 1;
			.b-dialog{
				bottom: 100%;
				right: calc(100% - 11.000em);
			}
		}
		&--lamp{
			width: 13.900em;
			height: 45.700em;
			background-image: url(../images/svg/lamp.svg);
			background-position: left bottom;
			bottom: calc(100% - 6.900em);
			right: calc(50% - 45.800em);
			z-index: 5;
			&:before{
				width: 0.200em;
				height: 500.000em;
				display: block;
				content: '';
				background: #7f7f81;
				position: absolute;
				bottom: 10.000em;
				left: calc(50% - 0.100em);
			}
			.b-dialog{
				bottom: 11.700em;
				left: calc(100% - 6.500em);
			}
		}
		&--calculator{
			width: 4.000em;
			height: 5.000em;
			background-image: url(../images/svg/calculator.svg);
			top: calc(50% - 4.300em);
			left: calc(50% + 5.200em);
		}
		&--board{
			width: 20.700em;
			height: 45.300em;
			background-image: url(../images/svg/board.svg);
			top: calc(50% - 31.300em);
			left: calc(50% + 13.500em);
			z-index: 2;
			.b-dialog{
				top: 9.600em;
				left: calc(100% + 2.200em);
			}
		}
		&--mirror{
			width: 39.900em;
			height: 28.000em;
			background-image: url(../images/svg/mirror.svg);
			bottom: calc(100% - 22.000em);
			right: calc(100% - 41.600em);
			z-index: 2;
			.b-dialog{
				bottom: calc(100% - 9.000em);
				left: calc(100% - 6.000em);
			}
		}
		&--photo{
			width: 15.200em;
			height: 15.000em;
			background-image: url(../images/svg/photo.svg);
			bottom: calc(100% - 5.200em);
			right: calc(100% - 22.100em);
			z-index: 1;
			.b-dialog{
				bottom: 2.600em;
				left: calc(100% + 1.500em);
			}
		}
		&--certified{
			width: 15.200em;
			height: 15.800em;
			background-image: url(../images/svg/certified.svg);
			bottom: calc(100% - 22.800em);
			right: calc(100% - 22.100em);
			z-index: 3;
			.b-dialog{
				bottom: 1.400em;
				right: calc(100% - 1.500em);
			}
		}
		&--strategy{
			width: 24.800em;
			height: 42.000em;
			background-image: url(../images/svg/strategy.svg);
			top: calc(50% - 21.400em);
			left: calc(50% - 8.600em);
			z-index: 5;
			.b-dialog{
				bottom: calc(50% + 7.100em);
				right: calc(100% - 1.600em);
			}
		}
		&--graph{
			width: 35.600em;
			height: 34.200em;
			background-image: url(../images/svg/graph.svg);
			bottom: calc(100% - 27.700em);
			left: calc(100% - 25.400em);
			z-index: 2;
			.b-dialog{
				bottom: calc(50% + 1.400em);
				right: calc(100% + 0.500em);
			}
		}
		&--two-man{
			width: 34.100em;
			height: 42.000em;
			background-image: url(../images/svg/two-man.svg);
			top: calc(50% - 21.400em);
			left: calc(50% - 8.600em);
			z-index: 5;
			.b-dialog{
				bottom: calc(50% + 10.900em);
				right: calc(100% + 2.400em);
			}
		}
	}
	&--main{
		width: 100%;
		height: 100%;
		background: $color-bg1;
		position: fixed;
		top: 0;
		left: 0;
		// .fp-tableCell{
		// 	@extend .b-flex;
		// 	@extend .b-flex--middle-col;
		// }
	}
	&.popover-is-open{
		&:before{
			width: 100%;
			height: 100%;
			display: block;
			content: '';
			background: $color-white;
			opacity: 0.7;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 99;
		}
		.b-dialog,
		.b-entry{
			opacity: 0 !important;
		}
		.uk-animation-fade{
			animation-name: uk-none;
		}
	}
}
.slideThree{
	.b-screen{
		&__img{
			&--buildL,
			&--buildR{
				opacity: 0 !important;
				&.uk-animation-fade{
					animation-name: uk-none;
				}
			}
		}
		&--main{
			background: $color-bg4;
		}
	}
}
.slideFour{
	.b-screen{
		&__img{
			&--buildL,
			&--buildR{
				opacity: 0 !important;
				&.uk-animation-fade{
					animation-name: uk-none;
				}
			}
		}
		&--main{
			background: $color-bg2;
		}
	}
}
.slideFive{
	.b-screen{
		&__img{
			&--door{
				opacity: 1;
				animation-name: uk-fade-bottom;
				animation-duration: 0.5s;
				animation-timing-function: ease-out;
				animation-fill-mode: both;
			}
			&--buildL,
			&--buildR{
				opacity: 0 !important;
				&.uk-animation-fade{
					animation-name: uk-none;
				}
			}
			&--flower{
				&:before{
					opacity: 0;
				}
			}
		}
		&--main{
			background: $color-bg5;
			.b-center{
				&:before{
					width: 3000px;
					height: 3000px;
					margin-left: -1500px;
					display: block;
					content: '';
					background: $color-bg6;
					position: absolute;
					top: calc(50% + 9.000em);
					left: 50%;
					left: 0;
					z-index: 1;
				}
			}
		}
	}
}
.b-btn-arrow{
    width: 4.000em;
    height: 4.000em;
    display: block;
    border: none;
    background: $color-green;
    border-radius: 100%;
    font-size: 1em;
    transition: all 0.25s ease;
    position: relative;
    z-index: 30;
    cursor: pointer;
    &:before{
        width: 4.000em;
        height: 4.000em;
        display: block;
        content: '';
        background: $color-green; 
        box-shadow: 0 0 0 0 $color-green;
        border-radius: 100%; 
        opacity: 0.5;
        animation: btn 1.5s linear infinite;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
    }
    &:after{
        width: 2.000em;
        height: 2.000em;
        margin: -1.000em 0 0 -1.000em;
        display: block;
        content: '';
        background-image: url(../images/svg/arrow.svg);
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
    }
}
#ipad,
#photo,
#lamp,
#two-man{
	&.b-btn-arrow{
		&:before{
			animation: btn1 1.5s linear infinite;
		}
	}
}
#table,
#certified,
#board{
	&.b-btn-arrow{
		&:before{
			animation: btn2 1.5s linear infinite;
		}
	}
}
#calendar,
#strategy,
#mirror{
	&.b-btn-arrow{
		&:before{
			animation: btn3 1.5s linear infinite;
		}
	}
}
#graph{
	&.b-btn-arrow{
		&:before{
			animation: btn4 1.5s linear infinite;
		}
	}
}
.b-dialog{
    width: 24.000em;
    height: 13.600em;
    padding: 1.200em 3.600em 6.000em;
    background: $color-white;
    border-radius: 12.000em / 6.300em;
    color: $color-body;
    text-align: center;
    position: relative;
    z-index: 99;
    @extend .b-flex;
    @extend .b-flex--middle-col;
    >*{
    	position: relative;
    	z-index: 10;
    }
    &:before,
    &:after{
        display: block;
        content: '';
        transform: rotate(45deg);
        position: absolute;
        bottom: -2.000em;
    }
    &:before{
        width: 0;
        height: 0;
        border-left: 1.500em solid transparent;
        border-right: 1.500em solid transparent;
        border-top: 4.000em solid $color-white;
        transform: rotate(45deg);
        left: 3.500em;
        z-index: 1;
    }
    &:after{
        width: 0.200em;
        height: 4.000em;
        background: $color-white;
        left: 4.800em;
        z-index: 2;
    }
    &--right{
        &:before,
        &:after{
            transform: rotate(-45deg);
            left: auto;
        }
        &:before{
            right: 3.500em;
        }
        &:after{
            right: 5.000em;
        }
    }
    &--center{
        &:before{
            border-left: none;
            border-top: 1em solid transparent;
            border-bottom: 2.000em solid transparent;
            border-right: 4.000em solid $color-white;
            transform: rotate(0deg);
            bottom: calc(50% - 2.000em);
            left: -2.300em;
        }
        &:after{
            display: none;
        }
    }
    &--center-r{
        &:before{
            border-right: none;
            border-top: 1em solid transparent;
            border-bottom: 2.000em solid transparent;
            border-left: 4.000em solid $color-white;
            transform: rotate(0deg);
            bottom: calc(50% - 2.000em);
            right: -2.300em;
            left: auto;
        }
        &:after{
            display: none;
        }
    }
    .b-btn-arrow{
        margin-left: -2.000em;
        position: absolute;
        bottom: 1.600em;
        left: 50%;
    }
    p{
    	font-size: 1.600em;
    	line-height: 1.200em;
    	letter-spacing: 0.03em;
    }
}
.b-entry{
    height: 27.600em;
    font-family: $ff-55Roma;
    font-weight: 500;
    color: $color-black;
    text-align: center;
    p{
    	font-size: 3.500em; 
    	line-height: 1.133em;
    }
}
.b-tit{
	padding: 0 0 1em;
	font-family: $ff-55Roma;
    font-size: 5.000em;
    font-weight: 700;
    font-weight: normal;
    line-height: 1.111em;
    span,
    strong{
        color: $color-green;
    }
    span{
        display: block;
    }
}
.b-btn{
	width: 100%;
	height: 4.500em;
	padding: 1em 1.375em;
	border: none;
	background: $color-green;
	border-radius: 0.250em;
	font-family: $ff-45Ligh;
	font-size: 1.898em;
	font-weight: 400;
	line-height: 1em;
	color: $color-white;
	position: relative;
	@extend .b-flex;
    @extend .b-flex--middle-col;
    span{
    	display: block;
    }
	&--play,
	&--read{
		padding-right: 4em;
		.icon{
		    width: 4.600em;
		    height: 4.600em;
		    margin-top: -2.200em;
		    display: block;
		    border: none;
		    background: $color-white;
		    border-radius: 100%;
		    font-size: 0.625em;
		    transition: all 0.25s ease;
		    position: absolute;
		    top: 50%;
		    right: 2.000em;
		    &:before{
		        width: 4.600em;
		        height: 4.600em;
		        display: block;
		        content: '';
		        background: $color-white; 
		        box-shadow: 0 0 0 0 $color-white;
		        border-radius: 100%; 
		        opacity: .5;
		        animation: btn-white 1.5s linear infinite;
		        position: absolute;
		        top: 0;
		        left: 0;
		        z-index: 1;
		    }
		    &:after{
		        width: 0;
		        height: 0;
		        display: block;
		        content: '';
		        transform: translate(-50%, -50%);
		        position: absolute;
		        top: 50%;
		        left: 50%;
		        z-index: 2;
		    }
		}
	}
	&--play{
		.icon{
			&:after{
		        width: 0;
		        height: 0;
		        border-top: 1em solid transparent;
		        border-bottom: 1em solid transparent;
		        border-left: 1.800em solid $color-green;
		    }
		}
	}
	&--read{
		.icon{
			&:after{
		        width: 2.800em;
		        height: 2.000em;
		        background-image: url(../images/svg/btn-read.svg);
		        background-position: 0 0;
				background-repeat: no-repeat;
				background-size: 100% auto;
		    }
		}
	}
}
.b-btn-close{
	width: 4.800em;
	height: 4.800em;
	display: block;
	border: none;
	background: $color-grey;
	border-radius: 100%;
	text-align: center;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-size: 1em;
	font-weight: 400;
	color: $color-white;
	position: relative;
	cursor: pointer;
	span{
		font-size: 3.600em;
		line-height: 1.333em;
	}
}
.popover {
	width: 48.500em;
	padding: 4.500em 3.500em 4.000em;
	display: none;
	background-color: $color-white;
	border: 0.200em solid $color-green;
	border-radius: 0.400em;
	font-weight: 400;
	color: $color-black;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1010;
	.arrow,
	.arrow:after{
		display: none;
	}
	.tit{
		padding: 0 0 1.250em;
		font-size: 3.000em;
		font-weight: normal;
		line-height: 1.100em;
		color: $color-green;
	}
	.entry{
		padding: 0 0 1.250em;
		font-size: 2.000em;
		line-height: 1.250em;
	}
	.b-btn-close{
		position: absolute;
		top: -2.200em;
		right: -2.200em;
	}
	.img{
		display: block;
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% auto;
		position: absolute;
		&--ipad{
			width: 10.000em;
			height: 15.800em;
			background-image: url(../images/svg/ipad-popover.svg);
			bottom: calc(100% - 2.800em);
			left: 4.000em;
		}
		&--calendar{
			width: 7.200em;
			height: 6.400em;
			background-image: url(../images/svg/calendar-popover.svg);
			bottom: calc(100% - 2.400em);
			left: 2.000em;
		}
		&--table{
			width: 13.400em;
			height: 6.200em;
			background-image: url(../images/svg/table-popover.svg);
			bottom: calc(100% - 1.300em);
		}
		&--lamp{
			width: 6.600em;
			height: 7.000em;
			background-image: url(../images/svg/lamp-popover.svg);
			bottom: calc(100% - 4.600em);
			left: -5.100em;
		}
		&--board{
			width: 15.400em;
			height: 5.000em;
			background-image: url(../images/svg/board-popover.svg);
			bottom: calc(100% + 0.100em);
			left: 0.800em;
		}
		&--mirror{
			width: 15.600em;
			height: 10.800em;
			background-image: url(../images/svg/mirror-popover.svg);
			bottom: calc(100% - 1.500em);
			left: 2.500em;
		}
		&--photo{
			width: 15.500em;
			height: 14.900em;
			background-image: url(../images/svg/photo-popover.svg);
			bottom: calc(100% - 2.000em);
			left: 4.500em;
		}
		&--certified{
			width: 15.300em;
			height: 15.800em;
			background-image: url(../images/svg/certified.svg);
			bottom: calc(100% - 2.600em);
			left: 2.600em;
		}
		&--strategy{
			width: 8.000em;
			height: 11.000em;
			background-image: url(../images/svg/strategy-popover.svg);
			bottom: calc(100% - 7.000em);
			right: calc(100% - 2.800em);
		}
		&--graph{
			width: 12.300em;
			height: 12.000em;
			background-image: url(../images/svg/graph-popover.svg);
			bottom: calc(100% - 3.300em);
			left: 2.900em;
		}
		&--two-man{
			width: 8.300em;
			height: 12.600em;
			background-image: url(../images/svg/man-popover.svg);
			top: 1.400em;
			right: calc(100% - 3.200em);
		}
	}
	&.right{
		margin: -10.000em 0 0 -1em;
	}
	&.left{
		margin: -14.000em 0 0 55em;
	}
	&.top{
		margin: -3em 0 0 3em;
	}
	&.bottom{
		margin: -12em 0 0 -5em;
	}
}
.slideThree{
	.popover{
		&.left{
			margin: -14.000em 0 0 45em;
		}
		&.right{
			margin: 0 0 0 -42.5em;
		}
		&.bottom{
			margin: -34em 0 0 22.6em;
		}
		&.top{
			margin: -7.7em 0 0 -9em;
		}
	}
}
.slideFour{
	.popover{
		&.right{
			margin: -14.5em 0 0 -29.5em;
		}
		&.bottom{
			margin: -7.5em 0 0 -25em;
		}
		&.top{
			margin: 21em 0 0 -5em;
		}
	}
}
.slideFive{
	.popover{
		&.top{
			margin: -6em 0 0 62.5em;
		}
	}
}


