.b-btns{
    bottom: 17px;
}
.b-btn-back,
.b-btn-next{
    padding: 0 7px;
    font-size: 10px;
    &:after{
        margin-top: 5px;
        border-left-width: 11px;
        border-right-width: 11px;
    }
    span{
        font-size: 10px;
        line-height: 10px;
    }
}
.b-btn-back{
    &:after{
        border-bottom-width: 13px;
    }
}
.b-btn-next{
    &:after{
        border-top-width: 13px;
    }
}
.b-btn-arrow{
    width: 25px;
    height: 25px;
    font-size: 10px;
    &:before{
        width: 25px;
        height: 25px;
        box-shadow: 0 0 0 0 $color-green;
    }
    &:after{
        width: 12px;
        height: 12px;
        margin: -6px 0 0 -6px;
    }
}
.b-dialog{
    width: 130px;
    height: 70px;
    padding: 16px 18px 34px;
    background: $color-white;
    border-radius: 65px / 35px;
    &:before,
    &:after{
        bottom: -15px;
    }
    &:before{
        width: 0;
        height: 0;
        border-left-width: 15px;
        border-right-width: 15px;
        border-top-width: 40px;
        left: 22px;
    }
    &:after{
        width: 2px;
        height: 40px;
        left: 37px;
    }
    &--right{
        &:before,
        &:after{
            transform: rotate(-45deg);
            left: auto;
        }
        &:before{
            right: 22px;
        }
        &:after{
            right: 37px;
        }
    }
    &--center{
        &:before{
            border-left: 15px solid transparent;
            border-right: 15px solid transparent;
            border-top: 40px solid $color-white;
            border-bottom: none;
            transform: rotate(-45deg);
            bottom: -15px;
            left: auto;
        }
        &:before{
            right: 22px;
        }
        &:after{
            right: 37px;
        }
    }
    .b-btn-arrow{
        margin-left: -12px;
        bottom: 5px;
    }
    p{
        font-size: 9px;
        line-height: 11px;
    }
}
.b-screen{
    padding: 90px 0 53px;
    .b-center{
        width: 100%;
        height: calc(100vh - 90px - 157px - 53px);
        max-height: 300px;
    }
    &__img{
        &--door{
            width: 224px;
            height: 223px;
            top: auto;
            bottom: 53px;
            left: calc(50% - 111px);
            opacity: 0;
        }
        &--buildL,
        &--buildR{
            width: 300px;
            height: calc(100% - 55px);
        }
        &--buildL{
            margin-left: -307px;
            left: 50%;
        }
        &--buildR{
            margin-right: -307px;
            right: 50%;
        }
        &--man{
            width: 103px;
            height: 241px;
            background-size: auto 100%;
            top: auto;
            bottom: 0;
            left: calc(50% - 66px);
        }
        &--flower{
            top: calc(100% - 55px);
            &:before{
                height: 6px;
            }
            span{
                width: 27px;
                height: 65px;
                top: calc(50% - 37px);
                right: 30px;
                &:first-child{
                    right: auto;
                    left: 30px;
                }
            }
        }
        &--ipad,
        &--ipad2{
            width: 41px;
            height: 52px;
            top: auto;
            bottom: 111px;
            left: calc(50% + 10px);
        }
        &--ipad2{
            .b-dialog{
                bottom: calc(100% + 15px);
                left: 4px;
            }
        }
        &--table{
            width: calc(50% - 9px);
            height: 27px;
            background: none;
            top: 0;
            bottom: auto;
            left: 0;
            .b-dialog{
                bottom: 0;
                left: auto;
                right: 0;
                &:before,
                &:after{
                    transform: rotate(-45deg);
                    left: auto;
                }
                &:before{
                    right: 22px;
                }
                &:after{
                    right: 37px;
                }
            }
        }
        &--calendar{
            width: 50%;
            height: 3px;
            background: none;
            top: auto;
            bottom: calc(100% + 5px);
            left: auto;
            right: 0;
            .b-dialog{
                bottom: 0;
                right: auto;
                left: 0;
                &--right{
                    &:before,
                    &:after{
                        transform: rotate(45deg);
                        right: auto;
                    }
                    &:before{
                        left: 22px;
                    }
                    &:after{
                        left: 37px;
                    }
                }
            }
        }
        &--lamp{
            width: calc(50% - 42px);
            height: 20px;
            background: none;
            bottom: 106px;
            right: auto;
            left: 20px;
            z-index: 5;
            &:before{
                display: none;
            }
            .b-dialog{
                bottom: 0;
                left: auto;
                right: 0;
                &:before,
                &:after{
                    transform: rotate(-135deg);
                    bottom: auto;
                    top: -15px;
                    left: auto;
                }
                &:before{
                    right: 22px;
                }
                &:after{
                    right: 37px;
                }
            }
        }
        &--calculator{
            width: 23px;
            height: 29px;
            top: auto;
            bottom: 117px;
            left: calc(50% + 12px);
        }
        &--board{
            width: 145px;
            height: 318px;
            top: auto;
            bottom: 16px;
            left: calc(50% + 71px);
            transform: translateX(0) !important;
            z-index: 2;
            .b-dialog{
                top: -17px;
                left: auto;
                right: calc(100% - 8px);
            }
            &.uk-animation-slide-right {
              animation-name: uk-fade-right1 !important;
            }
        }
        &--mirror{
            width: calc(50% - 42px);
            height: 20px;
            background: none;
            bottom: 215px;
            right: auto;
            left: 20px;
            .b-dialog{
                bottom: 0;
                left: auto;
                right: 0;
                &:before,
                &:after{
                    transform: rotate(-45deg);
                    left: auto;
                }
                &:before{
                    right: 22px;
                }
                &:after{
                    right: 37px;
                }
            }
        }
        &--photo{
            width: calc(50% - 42px);
            height: 20px;
            background: none;
            bottom: 213px;
            right: auto;
            left: 20px;
            .b-dialog{
                bottom: 0;
                left: auto;
                right: 0;
                &:before,
                &:after{
                    transform: rotate(-45deg);
                    left: auto;
                }
                &:before{
                    right: 22px;
                }
                &:after{
                    right: 37px;
                }
            }
        }
        &--certified{
            width: calc(50% - 42px);
            height: 20px;
            background: none;
            top: auto;
            bottom: calc(50% + 90px);
            left: 50%;
            .b-dialog{
                top: auto;
                bottom: 0;
                right: auto;
                left: 0;
                &--right{
                    &:before,
                    &:after{
                        transform: rotate(45deg);
                        top: auto;
                        bottom: -2em;
                        right: auto;
                    }
                    &:before{
                        left: 22px;
                    }
                    &:after{
                        left: 37px;
                    }
                }
            }
        }
        &--strategy{
            width: 145px;
            height: 241px;
            background-size: auto 100%;
            top: auto;
            bottom: 0;
            left: calc(50% - 66px);
            .b-dialog{
                bottom: calc(50% - 15px);
                right: calc(50% + 27px);
                &--center-r{
                    &:before,
                    &:after{
                        transform: rotate(-135deg);
                        bottom: auto;
                        top: -15px;
                        left: auto;
                    }
                    &:before{
                        border-bottom: none;
                        border-left: 15px solid transparent;
                        border-right: 15px solid transparent;
                        border-top: 40px solid $color-white;
                        right: 22px;
                    }
                    &:after{
                        display: block;
                        right: 37px;
                    }
                }
            }
        }
        &--graph{
            width: 173px;
            height: 166px;
            bottom: calc(50% + 32px);
            left: calc(50% + 61px);
            .b-dialog{
                bottom: calc(100% - 43px);
                right: calc(100% + 32px);
            }
        }
        &--two-man{
            width: 196px;
            height: 241px;
            background-size: auto 100%;
            top: auto;
            bottom: 0;
            left: calc(50% - 66px);
            .b-dialog{
                bottom: calc(100% + 18px);
                right: calc(50% + 31px);
            }
        }
    }
}
.slideFour,
.slideFive{
    .b-screen{
        &__img{
            &--flower{
                span{
                    display: none;
                    &:first-child{
                        display: block;
                    }
                }
            }
        }
    }
}
.slideFive{
    .b-screen{
        &--main{
            .b-center{
                &:before{
                    top: calc(100% - 53px);
                }
            }
        }
    }
}
.b-entry{
    height: 157px;
    padding: 10px 15px;
    p{
        font-size: 14px;
        line-height: 18px;
    }
}
.b-btn{
    width: 100%;
    height: 68px;
    padding: 10px 10px;
    border-radius: 3px;
    font-size: 12px;
    line-height: 16px;
    &--play,
    &--read{
        padding-right: 56px;
        .icon{
            width: 36px;
            height: 36px;
            margin-top: -18px;
            font-size: 10px;
            right: 10px;
            &:before{
                width: 36px;
                height: 36px;
                box-shadow: 0 0 0 0 $color-white;
            }
        }
    }
    &--play{
        .icon{
            &:after{
                border-top-width: 7px;
                border-bottom-width: 7px;
                border-left-width: 14px;
            }
        }
    }
    &--read{
        .icon{
            &:after{
                width: 22px;
                height: 16px;
            }
        }
    }
}
.b-btn-close{
    width: 30px;
    height: 30px;
    font-size: 10px;
    span{
        font-size: 20px;
        line-height: 30px;
    }
}
.popover {
    width: 282px;
    height: 428px;
    margin: 0 0 0 -141px !important;
    padding: 40px 17px 90px;
    border-width: 2px;
    border-radius: 4px;
    top: auto !important;
    bottom: 45px;
    left: 50% !important;
    .img{
        &--ipad{
            width: 62px;
            height: 98px;
            bottom: calc(100% - 15px);
            left: 24px;
        }
        &--calendar{
            width: 56;
            height: 50px;
            background-position: left bottom;
            bottom: calc(100% - 10px);
            left: 14px;
        }
        &--table{
            width: 102px;
            height: 48px;
            bottom: calc(100% - 10px);
            left: 9px;
        }
        &--lamp{
            width: 44px;
            height: 45px;
            bottom: calc(100% - 16px);
            left: 17px;
        }
        &--board{
            width: 117px;
            height: 38px;
            bottom: calc(100% - 11px);
            left: 10px;
        }
        &--mirror{
            width: 110px;
            height: 76px;
            bottom: calc(100% - 15px);
            left: 19px;
        }
        &--photo{
            width: 104px;
            height: 102px;
            bottom: calc(100% - 20px);
            left: 13px;
        }
        &--certified{
            width: 92px;
            height: 95px;
            bottom: calc(100% - 29px);
            left: 24px;
        }
        &--strategy{
            width: 61px;
            height: 83px;
            bottom: calc(100% - 20px);
            right: auto;
            left: 16px;
        }
        &--graph{
            width: 94px;
            height: 90px;
            bottom: calc(100% - 24px);
            left: 16px;
        }
        &--two-man{
            width: 61px;
            height: 98px;
            top: auto;
            bottom: calc(100% - 20px);
            right: auto;
            left: 14px;
        }
    }
    .tit{
        padding: 0 0 15px;
        font-size: 16px;
        line-height: 18px;
    }
    .entry{
        padding: 0 0 15px;
        font-size: 12px;
        line-height: 16px;
    }
    .b-btn-close{
        top: -17px;
        right: -17px;
    }
    .b-btn{
        width: auto;
        position: absolute;
        bottom: 18px;
        left: 17px;
        right: 17px;
    }
}
.b-tit{
    padding-bottom: 15px;
    font-size: 20px;
    line-height: 22px;
}
