@keyframes btn {
    from {
        box-shadow: 0 0 0 0 $color-green; 
        opacity: .5
    }
    to {
        box-shadow: 0 0 0 24px $color-green; 
        opacity: 0
    }
}
@keyframes btn-white {
    from {
        box-shadow: 0 0 0 0 $color-white; 
        opacity: .5
    }
    to {
        box-shadow: 0 0 0 24px $color-white; 
        opacity: 0
    }
}
