.b-page-slider {
    font-size: 7px;
}
.b-screen{
    .b-center{
        width: 100%;
    }
    &__img{
        &--lamp{
            .b-dialog{
                bottom: 9.7em;
                left: calc(100% - 11.000em);
            }
        }
        &--board{
            .b-dialog{
                left: 100%
            }
        }
        &--calendar{
            .b-dialog{
                right: calc(100% - 15.000em);
            }
        }
        &--certified{
            .b-dialog{
                top: calc(100% + 1em);
                bottom: auto;
                right: auto;
                left: calc(100% - 40px);
                &--right{
                    &:before,
                    &:after{
                        transform: rotate(160deg);
                        top: -2em;
                        bottom: auto;
                        right: auto;
                    }
                    &:before{
                        left: 3.500em;
                    }
                    &:after{
                        left: 5.000em;
                    }
                }
            }
        }
        &--graph{
            transform: translateX(0) !important;
            left: calc(100% - 40.400em);
            &.uk-animation-slide-right {
              animation-name: uk-fade-right1 !important;
            }
        }
    }
}
.b-entry{
    padding-left: 1em;
    padding-right: 1em;
    p{
        font-size: 3.000em; 
    }
}
.b-dialog{
    width: 20.000em;
    border-radius: 10.000em / 6.300em;
    p{
        font-size: 1.200em;
    }
}
.b-btn{
    font-size: 1.7em;
    // &--play,
    // &--read{
    //     padding-right: 4em;
    //     .icon{
    //         right: 2.000em;
    //     }
    // }
}
.popover {
    width: 39.900em;
    padding: 4.500em 2.000em 4.000em;
    &.right{
        margin-left: -6em;
    }
    &.left{
        margin-left: 45em;
    }
    &.top{
        margin: 2em 0 0 -6em;
    }
}
.slideThree{
    .popover{
        &.right{
            margin: 0 0 0 -32.5em;
        }
    }
}
.slideFour{
    .popover{
        &.right{
            margin-left: -36.5em;
        }
        &.bottom{
            margin-left: -24em;
        }
        &.top{
            margin-left: -18em;
        }
    }
}
.slideFive{
    .popover{
        &.top{
            margin-left: 52.5em;
        }
    }
}