*,
*::before,
*::after {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
    position: relative;
}
div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
button,
input[type="submit"]{
    border-radius: 0;
}
.cf {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--space-between-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--space-between-col {
        justify-content: space-between;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
